import { Grow } from "@mui/material";

import { withTableContainerHOC, withNavigationHOC, withRemoveItemWarningHOC } from "HOCs";
import { BooksTableBody, BooksTableHeader, BooksTableFilter, BooksPageHeader } from "./components";

const Books = () => {
    return (
        <>
            <BooksPageHeader />
            <Grow in={true} timeout={1000}>
                <table className="table" aria-label="Table of Books">
                    <thead className="table__header">
                        <BooksTableHeader />
                        <BooksTableFilter />
                    </thead>
                    <BooksTableBody />
                </table>
            </Grow>
        </>
    );
};

export default withRemoveItemWarningHOC(withTableContainerHOC(withNavigationHOC(Books)));
